import React, { Component } from 'react';
import { FaStar } from 'react-icons/fa';
export default class Testimonials extends Component {
	buildRating(rating){
		let ratingSet = [];
		let stars = [];
		for (let i = 0; i < rating; i++) {
			stars.push(<FaStar className="ratingStar highlighted" style={{ color: 'gold' }}/>);
		}
		ratingSet.push(<div className="rating">{stars}</div>);
					
		return ratingSet;
	}
	  render() {
		let resumeData = this.props.resumeData;
		return (
			<React.Fragment>
			<section id="testimonials">
				<div className="text-container">
				  <div className="row">
					<div className="two columns header-col">
					  <h1><span>Client Testimonials</span></h1>
					</div>
					<div className="ten columns flex-container">
					  <div className="flexslider">
						<ul className="slides">
						  {resumeData.testimonials.map((item, index) => (
							  <li key={index}>
								<blockquote>
								  <p>{item.quote}</p>
								  <cite>{item.client}</cite>
								  {this.buildRating(item.stars)}
								  <a href={item.source} target="_blank">{item.sourceName}</a>
								</blockquote>
							  </li>
						  ))}
						</ul>
					  </div> {/* div.flexslider ends */}
					</div> {/* div.flex-container ends */}
				  </div> {/* row ends */}
				</div>  {/* text-container ends */}
			  </section> {/* Testimonials Section End*/}
		  </React.Fragment>
		);
	  }
}