import React, { Component } from 'react';
export default class About extends Component {
	  render() {
		return (
			<React.Fragment>
			<section id="about">
				<div className="row">
				  <div className="three columns">
					<img className="profile-pic" src="images/profilepic.jpg" alt="Profile Picture" />
				  </div>
				  <div className="nine columns main-col">
					<h2>About Me</h2>
					<p>Born January 9th, 1980, I live and work as a freelance Digital Designer and Front Developer based in Prague, Czech Republic.</p>
					<p>I find users and how they interact with an interface fascinating, and is at the core of every project I work on.</p>
					<p>The experience I have gained through designing, developing and iterating on a multitude of experiences on a broad range of platforms has developed my instincts as a digital experience designer and front end developer.</p>
					<p>Using advanced development tools such as jQuery, ReactJS, lessCss/Sass and HTML5 has allowed me to design and develop effective UI for kiosks, web-based, mobile and tablet environments.</p>
					<p>My skills in art direction, creative digital design and illustration give me the flexability to produce high quality visual assets for use in any project.</p>
					<div className="row">
					  <div className="columns contact-details">
						<h2>Contact Details</h2>
						<p className="address">
						  <span>Jamie McDonnell</span><br />
						  <span>866 Emy Destinnove<br />
							Velke Prilepy 25264, Praha Zapad, CZ
						  </span><br />
						  <span>(+420)777 608 442</span><br />
						  <span>jamie.mcdonnell@gmail.com</span>
						</p>
					  </div>
					  <div className="columns download">
						<p>
						  <a href="resume-jamie-mcdonnell.pdf" className="button"><i className="fa fa-download" />Download Resume</a>
						</p>
					  </div>
					</div> {/* end row */}
				  </div> {/* end .main-col */}
				</div>
			</section> {/* About Section End*/}
		  </React.Fragment>
		);
	  }
}