import React, { Component } from 'react';

export default class Contact extends Component {
	  render() {
		return (
			<React.Fragment>
			<section id="contact">
				<div className="row section-head">
				  <div className="two columns header-col">
					<h1><span>Get In Touch.</span></h1>
				  </div>
				  <div className="ten columns">
					<p className="lead">If you are interested in collaborating on a new project, or would like to discuss how I might assist you in improving your existing product by analysing your user's behaviour, do not hesitate to get in touch - I am always interested in potential projects I can get my teeth into.</p>
				  </div>
				</div>
				<div className="row">
				  <div className="eight columns">
					{/* form */}
					<form method="post" id="contactForm" name="contactForm">
					  <fieldset>
						<div>
						  <label htmlFor="contactName">Name <span className="required">*</span></label>
						  <input type="text" size={35} id="contactName" name="contactName" />
						</div>
						<div>
						  <label htmlFor="contactEmail">Email <span className="required">*</span></label>
						  <input type="text" size={35} id="contactEmail" name="contactEmail" />
						</div>
						<div>
						  <label htmlFor="contactSubject">Subject</label>
						  <input type="text" size={35} id="contactSubject" name="contactSubject" />
						</div>
						<div>
						  <label htmlFor="contactMessage">Message <span className="required">*</span></label>
						  <textarea cols={50} rows={15} id="contactMessage" name="contactMessage" />
						</div>
						<div>
						  <button className="submit" onClick={this.handleClick}>Submit</button>
						  <span id="image-loader">
							<img alt="Loading Image" src="images/loader.gif" />
						  </span>
						</div>
					  </fieldset>
					</form> {/* Form End */}
					{/* contact-warning */}
					<div id="message-warning"> Error boy</div>
					{/* contact-success */}
					<div id="message-success">
					  <i className="fa fa-check" />Your message was sent, thank you!<br />
					</div>
				  </div>
				  <aside className="four columns footer-widgets">
					<div className="widget widget_contact">
					  <h4>Address and Phone</h4>
					  <p className="address">
						Jamie McDonnell<br />
						866 Emy Destinnove <br />
						Velke Prilepy 25264, Praha Zapad, CZ<br />
						<span>(+420) 777 608 442</span>
					  </p>
					</div>
				  </aside>
				</div>
			  </section> {/* Contact Section End*/}
		  </React.Fragment>
		);
	  }
}