import React, { Component } from 'react';
export default class Resume extends Component {
	  render() {
		let resumeData = this.props.resumeData;
		return (
			<React.Fragment>
			<section id="resume">
			<div className="row education">
			  <div className="three columns header-col">
				<h1><span>Education</span></h1>
			  </div>
			  <div className="nine columns main-col">
				{resumeData.education.map((item, id) => (
					<div className="row item" key={id}>
					  <div className="twelve columns">
						<h3>{item.place}</h3>
						<input type="checkbox" className="read-more-state" id={"education-post-"+id} />
						<p className="read-more-wrap info">
							{item.role} <span>•</span> <em className="date">{item.dates}</em>
							<span className="read-more-target"><br/>{item.description}</span>
						</p>
						<label htmlFor={"education-post-"+id} className="read-more-trigger" />
					  </div>
					</div>
				))}
			  </div> {/* main-col end */}
			</div> {/* End Education */}

			<div className="row work">
			  <div className="three columns header-col">
				<h1><span>Work</span></h1>
			  </div>
			  <div className="nine columns main-col">
				{resumeData.work.map((item, id) => (
					<div className="row item workEntry" key={id}>
					  <div className="twelve columns">
						<h3>{item.place}</h3>
						{item.roles.map((role, roleid) => (
							<div className="role" key={roleid}>
								<input type="checkbox" className="read-more-state" id={"work-post-"+id+"_"+roleid} />
								<p className="read-more-wrap info">{role.role} <span>•</span> <em className="date">{role.dates}</em>
									<span className="read-more-target"><br/>{role.description}</span>
								</p>
								<label htmlFor={"work-post-"+id+"_"+roleid} className="read-more-trigger" />
							</div>
						))}
					  </div>
					</div>
				))}
			  </div> {/* main-col end */}
			</div> {/* End Work */}
		  </section> {/* Resume Section End*/}
		  </React.Fragment>
		);
	  }
}