import React, { Component } from 'react';
export default class Portfolio extends Component {
	  constructor(props){
		  super(props);
		  this.state = {
			  portfolioTypes: []
		  }
		  this.togglePortfolioItems = this.togglePortfolioItems.bind(this);
	  }

	  togglePortfolioItems(item){
		  //render only portfolio items with selected tags
		  console.log("togglePortfolioItems", item);
		  let portfolioTypes = this.state.portfolioTypes;
		  if(!item.isToggleOn){
			  portfolioTypes.push(item.type);
		  }else{
			  portfolioTypes.splice(portfolioTypes.indexOf(item.type), 1);
		  }

		  this.setState({portfolioTypes: portfolioTypes});

		  console.log(this.state.portfolioTypes, portfolioTypes);
	  }
	
	  render() {
		let resumeData = this.props.resumeData;
		let togglePortfolioItems = this.togglePortfolioItems;
		let portfolioTypes = this.state.portfolioTypes;
		return (
			<React.Fragment>
			<section id="portfolio">
				<div className="row">
				  <div className="twelve columns collapsed">
					<h1>Check Out Some of My Works.</h1>
					<small className="text-center block">[Skill ------ Proficiency Level in %]</small>
					<div className="skillToggles">
						{resumeData.skills.map((item,index) => (
							<Skill 
								skill={item}
								key={index}
								togglePortfolioItems={togglePortfolioItems}
							/>
						))}
					</div>
					{/* portfolio-wrapper */}
					<div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
					  	{resumeData.portfolio.map((item,index) => (
							<GalleryItem 
						 		item={item}
								index={index}
								key={index}
								portfolioTypes={portfolioTypes}
							/>
						))}
					</div> {/* portfolio-wrapper end */}
				  </div> {/* twelve columns end */}
				  {/* Modal Popup
				  --------------------------------------------------------------- */}
				  {resumeData.portfolio.map((item,index) => (
					  <div key={index} id={"modal-0"+index} className="popup-modal mfp-hide">
						<img className="scale-with-grid" src={item.modalImagePath} alt="Gallery Image" />
						<div className="description-box">
						  <h4>{item.modalName}</h4>
						  <p>{item.modalDescription}</p>
						  <span className="categories"><i className="fa fa-tag" />
							{item.modalCategories.map((cat, index) => (
								<span key={index} className="skill">{cat}</span>
							))}
						  </span>
						</div>
						<div className="link-box">
						  <a href={item.url}>Details</a>
						  <a className="popup-modal-dismiss">Close</a>
						</div>
					  </div>
				  ))}
				</div> {/* row End */}
			  </section> {/* Portfolio Section End*/}
		  </React.Fragment>
		);
	  }
}

class Skill extends Component {
  constructor(props) {
    super(props);
    this.state = {
		isToggleOn: false,
		type: props.skill.name,
		hover: false
	};

    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
	this.hoverOn = this.hoverOn.bind(this);
	this.hoverOff = this.hoverOff.bind(this);
  }

  handleClick(e) {
	e.preventDefault();
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
	  
	this.props.togglePortfolioItems(this.state);
  }
	hoverOn(){
	  this.setState({ hover: true });
	}
	hoverOff(){ 
	  this.setState({ hover: false });    
	}
	
  render() {
	let skill = this.props.skill;
	let hover = this.state.hover;
	return (
		<React.Fragment>
		<span 
			className={"skill "+(this.state.isToggleOn ? 'on' : 'off')} 
			onClick={this.handleClick}
			onMouseEnter={this.hoverOn} 
			onMouseLeave={this.hoverOff}
		>
			<span className="skillName">{skill.name}</span>
			<span 
				style={{width: (hover ? skill.proficiency+"%" : "0%")}}
				className={"bar-expand "+skill.name.toLowerCase()} 
			/>
			<em>{skill.proficiency+"%"}</em>
		</span> {/* Skill Section End*/}
	  </React.Fragment>
	);
  }
}

class GalleryItem extends Component{
	constructor(props) {
		super(props);
		let portfolioTypes = this.props.portfolioTypes;
	}
	
	updateDisplay(){
		console.log("updateDisplay");
		let portfolioTypes = this.props.portfolioTypes;
		let item = this.props.item;
		let displayed = false;
		if(portfolioTypes.length === 0){
			displayed = true;
		}else{
			for(var x=0; x<portfolioTypes.length; x++){
				let cat = portfolioTypes[x];
				if(item.modalCategories.indexOf(cat) > -1){
					displayed = true;
				}
			};
		}
		
		return displayed;
	}
	
	render() {
		let item = this.props.item;
		let index = this.props.index;
		var displayed = this.updateDisplay();
		
		return (
			<React.Fragment>
			<div className={"columns portfolio-item "+(displayed ? "" : "hide ")+item.modalCategories.sort().join(" ")}>
				<div className="item-wrap">
				  <a href={"#modal-0"+index} title={item.name}>
					<img alt="Gallery Image" src={item.imagePath} />
					<div className="overlay">
					  <div className="portfolio-item-meta">
						<h5>{item.name}</h5>
						<p>{item.description}</p>
					  </div>
					</div>
					<div className="link-icon"><i className="icon-plus" /></div>
				  </a>
				</div>
			  </div>
		  </React.Fragment>
		);
	  }
}