import React, { Component } from 'react';
export default class Header extends Component {
	  render() {
		let resumeData = this.props.resumeData;
		return (
		<React.Fragment>
		  <header id="home">
			<nav id="nav-wrap">
			  <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
			  <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
			  <ul id="nav" className="nav">
				<li className="current"><a className="smoothscroll" href="#home">Home</a></li>
				<li><a className="smoothscroll" href="#about">About</a></li>
				<li><a className="smoothscroll" href="#resume">Resume</a></li>
				<li><a className="smoothscroll" href="#portfolio">Works</a></li>
				<li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
				<li><a className="smoothscroll" href="#contact">Contact</a></li>
			  </ul> {/* end #nav */}
			</nav> {/* end #nav-wrap */}
			<div className="row banner">
			  <div className="banner-text">
				<h1 className="responsive-headline">Hi, I'm {resumeData.name}.</h1>
				<h3>I'm a {resumeData.role}. Let's <a className="smoothscroll" href="#about">start scrolling </a>
				  and learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
				<hr />
				<ul className="social">
				  <li><a href="http://linkedin.com/in/jamiemcdonnell" target="_blank" rel="noopener noreferrer" title="Visit my linkedIn Profile"><i className="fa fa-linkedin" /></a></li>
				  <li><a href="https://github.com/JamieMcDonnell" target="_blank" rel="noopener noreferrer" title="Visit my GitHub Profile"><i className="fa fa-github" /></a></li>
				  <li><a href="https://www.upwork.com/o/profiles/users/_~016bafa6da0ac3a402/" target="_blank" rel="noopener noreferrer" title="Visit my Upwork Profile"><i className="fa fa-globe" /></a></li>
				</ul>
			  </div>
			</div>
			<p className="scrolldown">
			  <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
			</p>
		  </header> {/* Header End */}
			</React.Fragment>
		);
	  }
}